/* ==========================================================================
   Centered Title type style - begin
   ========================================================================== */

.mkdf-title-holder {
	
	&.mkdf-centered-type {
		
		.mkdf-title-inner {
			text-align: center;
		}
		
		.mkdf-page-title {
			margin: 0;
			font-weight: 400;
			line-height: 1em;
			color: $additional-heading-color;
		}
		
		.mkdf-page-subtitle {
			display: block;
			margin: 26px 0 0;
			box-sizing: border-box;
			font-weight: 400;
			line-height: 1.875em;
			color: $default-text-color;
			letter-spacing: 0;
		}
	}
}
/* ==========================================================================
   Centered Title type style - end
   ========================================================================== */