/* ==========================================================================
   Standard With Breadcrumbs Title type style - begin
   ========================================================================== */

.mkdf-title-holder {
	
	&.mkdf-standard-with-breadcrumbs-type {
		
		.mkdf-title-info,
		.mkdf-breadcrumbs-info {
			display: block;
			vertical-align: middle;
		}
		
		.mkdf-breadcrumbs-info {
			white-space: nowrap;
			margin-top: 10px;
		}
		
		.mkdf-page-title {
			margin: 0;
			font-weight: 400;
		}
		
		.mkdf-page-subtitle {
			margin: 5px 0 0;
		}
		
		.mkdf-breadcrumbs {
			color: $additional-text-color;
			
			&.mkdf-has-inline-style {
				
				a {
					opacity: 1;
					@include mkdfTransition(opacity .2s ease-out);
					
					&:hover {
						color: inherit;
						opacity: 0.9;
					}
				}
			}
			
			a,
			span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				color: inherit;
			}

			.mkdf-current {
				color: $first-main-color;
			}
			
			a {
				i {
					position: relative;
					top: 2px;
				}

				&:hover {
					color: $first-main-color;
				}
			}
		}
	}
}
/* ==========================================================================
   Standard With Breadcrumbs Title type style - end
   ========================================================================== */